import { Container, Flex, Grid, IconButton } from "@chakra-ui/react";
import { Document, Page } from "react-pdf";
import { useEffect, useState, useRef } from "react";
import { ArrowLeftIcon, ArrowRightIcon } from "@chakra-ui/icons";

const FilePreview = ({ file }) => {
  const [page, setPage] = useState(1);
  const [pdfContainerWidth, setPdfContainerWidth] = useState(500);
  const pdfContainerRef = useRef(null);
  useEffect(() => {
    const handleResize = () => {
      console.log("hello")
      const newWidth = 
        pdfContainerRef.current.getBoundingClientRect().width;
      if (pdfContainerWidth !== newWidth) {
        console.log("set")
        setPdfContainerWidth(newWidth);
      }
    }
    window.addEventListener("resize", handleResize);
    return window.removeEventListener("resize", handleResize);

  }, [file])
  return (
    <Grid placeItems="center" ref={pdfContainerRef}>
      <Container>
        <Document file={file}>
          <Page
          pageNumber={page} width={pdfContainerWidth}
          loading={"please wait"}
          />
        </Document>
      </Container>
      <Flex>
        <IconButton
          onClick={() => setPage((current) => (current - 1))}
          isDisabled={page === 1}
          icon={<ArrowLeftIcon />} 
          />
        <IconButton 
          onClick={() => {setPage((current) => current + 1)}}
          isDisabled={page === file.numPages - 1}
          icon={<ArrowRightIcon />}
        />

      </Flex>
    </Grid>
  );
};

export default FilePreview;
